import React, { Component } from 'react';
import './App.css';
import Nav from './components/nav';
import PanelHome from './components/panelHome';
import PanelInfo from './components/panelInfo';
import PanelTestimonials from './components/panelTestimonials';
import PanelFeatures from './components/panelFeatures';
import PanelPricing from './components/panelPricing';
import PanelBeta from './components/panelBeta';
import PanelContact from './components/panelContact';

class App extends Component {
  state = {
    list: ["1", "2", "3", "4"]
  };
  handleClick = (id) => {
    let x = this.state.list;
    x.push(id + "_r_" + (x.length + 1) );
    this.setState({ list: x });
  };
  render() {
    return (
      <div className="App">
        <Nav />
        <PanelHome />
        <PanelInfo />
        <PanelTestimonials />
        <PanelFeatures />
        <PanelPricing />
        <PanelBeta />
        <PanelContact />
      </div>
    );
  }
}

export default App;


