import React, { Component } from 'react';
import './panels.css';
import './panelFeatures.css';

class PanelFeatures extends Component {
	state = {
		features: [
			{ 
				image: "features_compare.png",
				title: "Compare and select",
				text: "Compare two candidates at a time with our side-by-side comparison tool. The most objective way to find the best candidate."
			},
			{ 
				image: "features_feedback.png",
				title: "Feedback",
				text: "Easily add feedback about a candidate at any step of the precess. Later on youll be able to review this feedback, helping you remember your thought."
			},
			{ 
				image: "features_notifications.png",
				title: "Interviewer notifications",
				text: "Assign an interviewer(s) to a certain candidate and we'll make sure they never miss or overbook an interview slot."
			},
			{ 
				image: "features_scoring.png",
				title: "Scoring",
				text: "Using your feedback and the martching skills of a candidate to a certain role, our Machine Learning algorithms are able to score each candidate, allowing you to make faster and more objective decisions."
			},
			{ 
				image: "features_suggestions.png",
				title: "Interview suggestions",
				text: "After you enter your job description well be able to suggest up-to-date suggestions on both interview questions and tasks for candidate to complete. Helping not only rookie interviewers but the most experienced ones too!"
			},
			{ 
				image: "features_site.png",
				title: "A beautiful career site",
				text: "Your career site is the center of all the information about your workplace. Give your candidates the best experience with a modern career site, tailored for your company."
			},
			{ 
				image: "features_mobile.png",
				title: "Mobile recruiting",
				text: "We didnt stop at making the career site mobile responsive, we made the whole application process mobile responsive. With Mustered, you give the candidate possibilities to apply the way they want, whenever they want to."
			},
			{ 
				image: "features_share.png",
				title: "Integrated with social media",
				text: "Reach your candidates where they are, and get the maximum spread of your ads in social media. Now everybody can apply for a job with the click of a button, using our innovative integration with LinkedIn and Facebook."
			},
		]
	}
    render() {
        return (
    		<div id="features" className="panel panelFeatures">
    			<h2>Features</h2>
    			<div className="leftFeatures">
    				{this.state.features.map(item => 
    					<div className="_row" key={item.title}>
	    					<div className="left"><img className="wow rollIn" src={'images/' + item.image} alt={item.title} /></div>
	    					<div className="right">
	    						<h3>{item.title}</h3>
	    						<p>{item.text}</p>
	    					</div>
	    				</div>
    				)}
    			</div>
    		</div>
        );
    }
}

export default PanelFeatures;



