import React, { Component } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll'
import './panelHome.css';

class PanelHome extends Component {
	state = {
		integrations: [
			{ image: "logo_slack.png", name: "Slack" },
			{ image: "logo_trello.png", name: "Trello" },
			{ image: "logo_linkedin.png", name: "LinkedIn" },
		],
		partners: [
			{ image: "logo_techcrunch.png", name: "TechCrunch" },
			{ image: "logo_mashable.png", name: "Mashable" },
			{ image: "logo_medium.png", name: "Medium" },
		]
	}
    render() {
        return (
    		<div id="home" className="panelHome">
    			<div className="wow slideInLeft blurredImage">
    				<div className="container_1">
    					<h1>Mustered</h1>
    					<p>Easily manage your companys entire hiring process. 
    					From candidates to interviews to feedback to hires.
    					All in one place.<br />
    					A career site of your own.</p>
    					<h3>1-click <span className="fas fa-mouse-pointer"></span> integrations</h3>
						<div className="container">
							<div className="row">
								{this.state.integrations.map(item => 
						          	<div className="col-sm" key={item.name}>
										<img src={"/images/" + item.image} alt={item.name} />
									</div>
								)}
					        </div>
						</div>    					
    					<h3>As seen on <span className="far fa-eye"></span></h3>
    					<div className="container">
							<div className="row">
								{this.state.partners.map(item => 
						          	<div className="col-sm" key={item.name}>
										<img src={"/images/" + item.image} alt={item.name} />
									</div>
								)}
							</div>
						</div>
    				</div>
    			</div>
    			<div className="next">
					<AnchorLink offset="87" href={"#about"}><i className="wow heartBeat fas fa-angle-down" data-wow-iteration="100"></i></AnchorLink>
				</div>
    		</div>        
        );
    }
}

export default PanelHome;



