import React, { Component } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll'
import './nav.css';

class Nav extends Component {
	state = {
		nav: [
			{id: 1, title: 'Home', type: 'active', url: '#home'},
			{id: 2, title: 'About', type: 'active', url: '#about'},
			{id: 3, title: 'Features', type: 'active', url: '#features'},
			{id: 4, title: 'Pricing', type: 'active', url: '#pricing'},
			{id: 5, title: 'Contact', type: 'active', url: '#contact'},
		],
		active: false
	};

	constructor(){
		super();
		const _this = this;
		window.onscroll = function() { _this.scrollFunction() };
	}

	scrollFunction() {
		const h_panel = document.getElementById('home').getBoundingClientRect().height;
		const h_nav = document.getElementById('fixed-top').getBoundingClientRect().height;
		const h = h_panel - h_nav;
		if (document.body.scrollTop > h || document.documentElement.scrollTop > h) {
        	this.setState({ active: true });
		} else {
        	this.setState({ active: false });
		}
	}

    render() {
        return (
        	<nav id="fixed-top" className={"fixed-top " + (this.state.active ? 'active': null)}>
	    		<ul className="nav">
	    			<li key="ham" className="nav-item hamburger">
	    				<i className="fas fa-bars"></i>
	    			</li>
					{this.state.nav.map(item =>
						<li key={item.id} className="nav-item link">
							<AnchorLink offset="87" href={item.url} className="nav-link text-white {item.type}">{item.title}</AnchorLink>
						</li>
					)}
				</ul>
				<div key={"signup"} className="signup_login justify-content-end">
					<AnchorLink offset="87" href={'#beta'} className="nav-link text-white mr-3" style={{paddingTop: 12}}>Login</AnchorLink>
					<AnchorLink offset="87" href={'#beta'} className="btn btn-lg btn-outline-light">Sign up</AnchorLink>
				</div>
			</nav>
        );
    }
}

export default Nav;



