import React, { Component } from 'react';
import './panels.css';
import './panelBeta.css';

class PanelBeta extends Component {
    render() {
        return (
            <div id="beta" className="panel panelBeta">
            	<h2>Get Early Access</h2>
            	<p>Due to high demand, we are giving early access to the Mustered platform to some companies on a first come first served basis.<br />To get access simply fill in the form below and we'll set up an account for you as soon as possible.</p>
            	<form id="mc-form" className="betaForm">
				   	<input type="email" name="EMAIL" className="email" placeholder="Your Email Address" />
				   	<input type="submit" value="Get Notified" name="subscribe" className="button large" />
				</form>
				<p style={{maxWidth: "90%", margin: "0 auto 100px auto"}}><i style={{color: "rgba(0, 0, 0, 0.2", padding: 10}} className="fas fa-quote-left"></i><i>People are not your most important asset. The right people are.</i><i style={{color: "rgba(0, 0, 0, 0.2", padding: 10}} className="fas fa-quote-right"></i><br />Jim Collins</p>
            </div>
        );
    }
}

export default PanelBeta;



