import React, { Component } from 'react';
import './panels.css';
import './panelTestimonials.css';

class PanelTestimonials extends Component {
    render() {
        return (
            <div className="panel panelTestimonials">
            	<h2>Our Inspiration</h2>
            	<p style={{maxWidth: "90%", margin: "0 auto 10px auto", color: "#fff"}}><i style={{color: "rgba(255, 255, 255, 0.2", padding: 10}} className="fas fa-quote-left"></i><i>The key for us, number one, has always been hiring very smart people.</i><i style={{color: "rgba(255, 255, 255, 0.2", padding: 10}} className="fas fa-quote-right"></i><br />Bill Gates</p>
            	<p style={{maxWidth: "90%", margin: "0 auto 40px auto", color: "#fff"}}><i style={{color: "rgba(255, 255, 255, 0.2", padding: 10}} className="fas fa-quote-left"></i><i>If you think it's expensive to hire a professional to do the job, wait until you hire an amateur.</i><i style={{color: "rgba(255, 255, 255, 0.2", padding: 10}} className="fas fa-quote-right"></i><br />Red Adair</p>
            </div>
        );
    }
}

export default PanelTestimonials;



