import React, { Component } from 'react';
import './panels.css';
import './panelContact.css';

class PanelContact extends Component {
    render() {
        return (
            <div id="contact" className="panel panelContact">
            	<h2>Get In Touch</h2>
            	<p><i class="far fa-envelope"></i> info@mustered.io</p>
                <p><i class="far fa-copyright"></i> Copyright 2018 | Mustered</p>
            </div>
        );
    }
}

export default PanelContact;



