import React, { Component } from 'react';
import './panels.css';
import './panelInfo.css';

class PanelInfo extends Component {
    render() {
        return (
    		<div id="about" className="panel panelInfo">
    			<h2>How it works</h2>
    			<div className="centeredFeatures">
    				<div className="row">
    					<div className="left">
    						<h3>The Candidates</h3>
    						<p>After creating a job opening allows candidates to apply to it via your automated landing page. Access applications and send interview invites though our intelligently design dashboard. Keeping it all tidy in one place.</p>
    					</div>
    					<div className="middle"><img className="wow fadeIn" src={'images/circle_1.png'} alt={'The Candidates'} /></div>
    					<div className="right"><img className="wow slideInRight dashboard" src={'images/platform_dashboard.png'} alt={'Dashboard'} /></div>
    				</div>
    				<div className="row">
    					<div className="left"><img className="wow slideInLeft candidate" src={'images/platform_candidate.png'} alt={'Cadndidate'} /></div>
    					<div className="middle"><img className="wow fadeIn" src={'images/circle_2.png'} alt={'The Interviews'} /></div>
    					<div className="right">
    						<h3>The Interviews</h3>
    						<p>As the interviews take place, the interviewers can add any feedback regarding the candidates, including some metrics that will later help you make a better decision on whom to hire. On top of that our clever bot will suggest some great interview questions related to the role.</p>
    					</div>
    				</div>
    				<div className="row">
    					<div className="left">
    						<h3>Your Decision</h3>
    						<p>After all interviews are over and all feedback has been entered our AI will objectively identify the best candidate for the role. Although at the end of the day its you who has the final decision.</p>
    					</div>
    					<div className="middle"><img className="wow fadeIn" src={'images/circle_3.png'} alt={'Your Decision'} /></div>
    					<div className="right"><img className="wow slideInRight decision" src={'images/platform_decision.png'} alt={'Decision'} /></div>
    				</div>
    				<div className="row">
    					<div className="left"><img className="wow slideInLeft site" src={'images/platform_site.png'} alt={'Career site'} /></div>
    					<div className="middle"><img className="wow fadeIn" src={'images/circle_4.png'} alt={'Your New Career Site'} /></div>
    					<div className="right">
    						<h3>Your New Career Site</h3>
    						<p>To top it all up we will build a beautiful and attractive career site for you which will showcase your company, team, culture and any perks you may offer, helping you attract the best candidates.</p>
    					</div>
    				</div>
    			</div>
    		</div>        
        );
    }
}

export default PanelInfo;



